<template>
  <div class="listView">
    <listview-top :playlist="state.playlist"></listview-top>
    <play-list :playlist="state.playlist"></play-list>
  </div>
</template>

<script>
import listviewTop from '@/components/listviewTop.vue'
import playList from '@/components/playList.vue' 
import {getPlaylistDetail} from '@/api/index.js'
import {onMounted,reactive} from 'vue'
import {useRoute} from 'vue-router'
import store from '@/store/index.js'
export default {
    setup(){
        const route = useRoute()
        let state = reactive({
          list:[],
          playlist:{
            creator:{},
            tracks:[]
          }
          })
        onMounted(async ()=>{
            let id = route.query.id
            console.log(route)
            let result = await getPlaylistDetail(id)
            state.playlist = result.data.playlist
            state.playlist.tracks.forEach((item,i)=> {
              store.commit('pushPlaylist',item)
            });
            
            console.log(result)
            
        })
        return {state}
        
    },
    components:{
      listviewTop,
      playList
    }
}
</script>

